.day-status,.day-new-status {
	background: url(../../img/wx/laikan/chapter-day-new-bg.jpg);
	.txtbox-item{
		h3{
			color: #666;
		}
	}
	.demo .font-change a{
		border: 1px solid #6cd;
	}
	.intro .chapter{
		border-right: 1px solid #ccc;
		color: #333;
		i{
			background: #ccc;
			border: 2px solid #eee;
			&.red{
				background: #ccc;
			}
		}
	}
	.font_list{
		border-top: solid 1px #ccc;
		a{
			color: #999;
		}
	}
	.alert-c{
		color: #333;
	}
	.intro div{
		color: #333;
	}
	.blue{
		color: #6cd!important;
		display: inline-block;
		font-style: normal;
	}
	.chapter-mod-list li a{
		color: #6cd;
		border: 1px solid #6cd;
		&.first{
			border: 1px solid #ccc;
			color: #999;
			background: #ddd;
		}
	}
	.chapter-time{
		border-bottom:solid 1px #e5e5e5;
		color:#999;
	}
	.chapter-mod-div{
		border-top:solid 1px #e5e5e5;
	}
	.chapter_spread li a span{
		color: #666;
	}
	.chapter_ma span{
		background: #ebebeb;
	}
	.chapter-collect{
		float:left;
		margin:1rem 0rem .5rem 1.5rem;
		a{
		    padding:2px 8px 3px;
		    display: inline-block;
		    border-radius: 3px;
		    color:#2be;
		    border: 1px solid #2be;
		}
	}
	.chapter-app-down{
		border-bottom:solid 1px #ccc;
		a{
			color:#fff;
			background:#2be;
		}
	}
	.chapter_spread li a p.r{
		padding-bottom:1rem;
		border-bottom:solid 1px #ccc;
	}
}

.day-new-status{
	background: url(../../img/mobile/chapter_bg.jpg);
	.chapter-mod-div{
		margin-top:0rem;
		border-top:none;
	}
	.chapter-spread-ma{
		border-bottom:solid 1px #ccc;
		border-top:solid 1px #ccc;
	}
	.chapter-mod-list li a{
		color:#2be;
		border:solid 1px #2be;
	}
	.collent-dialog{
		.chapter-spread-ma{
			border:none;
		}
	}
	.chapter-app-down {
		.chapter-app-text{
			color:#333;
			background:#f6f6f6;
		}
	}
	
}
